'use client'

import { useState } from 'react'
import { Button } from "@/components/ui/button"
import Link from "next/link"

interface Inspection {
  id: string
  customer: string
  date: string
  type: 'routine' | 'sikkerhet' | 'kvalitet'
  status: 'planlagt' | 'fullført' | 'avlyst'
  inspector: string
  notes: string
}

export default function InspeksjonerDashboard() {
  const [inspections, setInspections] = useState<Inspection[]>([
    {
      id: '1',
      customer: 'Acme AS',
      date: '2024-03-20',
      type: 'routine',
      status: 'planlagt',
      inspector: 'Ole Hansen',
      notes: 'Rutinemessig inspeksjon av sikkerhetssystemer'
    },
    // ... flere inspeksjoner
  ])

  const [showAddModal, setShowAddModal] = useState(false)
  const [newInspection, setNewInspection] = useState<Partial<Inspection>>({})

  return (
    <div className="min-h-screen bg-gray-900 p-8 ml-64">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="flex justify-between items-center mb-12">
          <div>
            <h1 className="text-4xl font-bold text-white">Inspeksjoner</h1>
            <p className="text-gray-400 mt-2">Administrer inspeksjoner og rapporter</p>
          </div>
          <button
            onClick={() => setShowAddModal(true)}
            className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-full flex items-center gap-2 shadow-sm transition-all duration-200"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
            </svg>
            <span>Ny inspeksjon</span>
          </button>
        </div>

        {/* Statistikk-kort */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <div className="bg-gray-800 rounded-2xl p-6 border border-gray-700">
            <div className="flex items-center justify-between">
              <h3 className="text-gray-400">Planlagte inspeksjoner</h3>
              <div className="w-10 h-10 rounded-full bg-blue-900/50 flex items-center justify-center">
                <svg className="w-5 h-5 text-blue-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" />
                </svg>
              </div>
            </div>
            <p className="text-2xl font-bold text-white mt-2">8</p>
          </div>

          <div className="bg-gray-800 rounded-2xl p-6 border border-gray-700">
            <div className="flex items-center justify-between">
              <h3 className="text-gray-400">Fullførte denne måneden</h3>
              <div className="w-10 h-10 rounded-full bg-green-900/50 flex items-center justify-center">
                <svg className="w-5 h-5 text-green-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                </svg>
              </div>
            </div>
            <p className="text-2xl font-bold text-white mt-2">12</p>
          </div>

          <div className="bg-gray-800 rounded-2xl p-6 border border-gray-700">
            <div className="flex items-center justify-between">
              <h3 className="text-gray-400">Avlyste inspeksjoner</h3>
              <div className="w-10 h-10 rounded-full bg-red-900/50 flex items-center justify-center">
                <svg className="w-5 h-5 text-red-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </div>
            </div>
            <p className="text-2xl font-bold text-white mt-2">2</p>
          </div>
        </div>

        {/* Filtrering og søk */}
        <div className="bg-gray-800 rounded-2xl p-6 mb-8 border border-gray-700">
          <div className="flex flex-wrap gap-4">
            <input
              type="text"
              placeholder="Søk etter kunde eller inspektør..."
              className="flex-1 min-w-[200px] rounded-xl border-gray-700 focus:ring-blue-500 focus:border-blue-500 bg-gray-700 text-gray-300"
            />
            <select className="rounded-xl border-gray-700 focus:ring-blue-500 focus:border-blue-500 bg-gray-700 text-gray-300">
              <option value="all">Alle typer</option>
              <option value="routine">Rutine</option>
              <option value="sikkerhet">Sikkerhet</option>
              <option value="kvalitet">Kvalitet</option>
            </select>
            <select className="rounded-xl border-gray-700 focus:ring-blue-500 focus:border-blue-500 bg-gray-700 text-gray-300">
              <option value="all">Alle status</option>
              <option value="planlagt">Planlagt</option>
              <option value="fullført">Fullført</option>
              <option value="avlyst">Avlyst</option>
            </select>
          </div>
        </div>

        {/* Inspeksjonsliste */}
        <div className="bg-gray-800 rounded-2xl border border-gray-700 overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead>
                <tr className="border-b border-gray-700">
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">Kunde</th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">Dato</th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">Type</th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">Status</th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">Inspektør</th>
                  <th className="px-6 py-4 text-right text-sm font-medium text-gray-400">Handling</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-700">
                {inspections.map(inspection => (
                  <tr key={inspection.id} className="hover:bg-gray-700/50">
                    <td className="px-6 py-4">
                      <div className="text-white">{inspection.customer}</div>
                    </td>
                    <td className="px-6 py-4">
                      <div className="text-white">{new Date(inspection.date).toLocaleDateString('nb-NO')}</div>
                    </td>
                    <td className="px-6 py-4">
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                        inspection.type === 'routine'
                          ? 'bg-blue-900/50 text-blue-400'
                          : inspection.type === 'sikkerhet'
                          ? 'bg-red-900/50 text-red-400'
                          : 'bg-green-900/50 text-green-400'
                      }`}>
                        {inspection.type}
                      </span>
                    </td>
                    <td className="px-6 py-4">
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                        inspection.status === 'planlagt'
                          ? 'bg-yellow-900/50 text-yellow-400'
                          : inspection.status === 'fullført'
                          ? 'bg-green-900/50 text-green-400'
                          : 'bg-red-900/50 text-red-400'
                      }`}>
                        {inspection.status}
                      </span>
                    </td>
                    <td className="px-6 py-4">
                      <div className="text-white">{inspection.inspector}</div>
                    </td>
                    <td className="px-6 py-4 text-right">
                      <div className="text-white">
                        <Link href={`/inspeksjoner/${inspection.id}`} className="text-blue-400 hover:text-blue-500 mr-2">
                          <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7.06-9 9.542-9C21.477 5 17.687 7.943 16.414 12c1.274 4.057 5.064 7 9.542 7c-1.274 4.057-5.064 7-9.542 7c-4.478 0-8.268-2.943-9.542-7z" />
                          </svg>
                        </Link>
                        <button className="text-red-400 hover:text-red-500">
                          <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                          </svg>
                        </button>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  )
} 